/*@import "bootstrap/bootstrap";*/

@btn-font-weight: 300;
@font-family-sans-serif: "Roboto", Helvetica, Arial, sans-serif;

body, label, .checkbox label {
	font-weight: 300;
}

section {
	text-align: center;
	box-sizing: border-box;
	display: inline-block;
	vertical-align: top;

}

.card {
	display: inline-block;
	background: #f3f3ff;
	box-shadow: 5px 5px 33px 0 rgba(0, 0, 24, 0.13);
	margin: 0 auto;
	box-sizing: border-box;
	min-width: 90px;
	min-height: 90px;
	text-align: center;
	margin: 0 auto 15px;
	list-style: none;
	color: #63a3ff;
}
